main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.page-title {
  font-size: 9rem;
  color: var(--white);
  text-shadow: 3px 3px var(--black);
  text-align: center;
}

.page-blurb {
  font-size: 3rem;
  text-align: center;
}

.vocab-container {
  padding: 2rem 4rem;
  display: grid;
  place-content: center;
  grid-template-columns: 1fr 1fr;
  text-align: center;
}

.vocab-word {
  margin: 3.2rem 0 0 0;
  font-size: 4.2rem;
  color: var(--white);
  text-shadow: 3px 3px var(--black);
}

.vocab-def {
  margin: 5px 0 0 0;
  font-size: 3rem;
}

.vocab-text button {
  width: auto;
  padding: 1rem 2rem;
}

@media only screen and (max-width: 600px) {
  
  .page-title {
    font-size: 6rem;
    margin-bottom: 1rem;
  }
  
  .vocab-container {
    padding: 1rem 2.5rem;
  }
  
  .vocab-word {
    margin: 2.2rem 0 0 0;
    font-size: 3.2rem;
  }
  
  .vocab-def {
    margin: 2px 0 0 0;
    font-size: 2.2rem;
  }
  
 .vocab-text button {
    width: auto;
    padding: 1rem 2rem;
  }
  
  

}