.button {
  font-size: 2rem;
  font-family: "Viga", sans-serif;
  border-radius: 1rem;
  border: none;
  padding: 1rem 2.4rem;
  background-color: var(--blue);
  border: 3px solid rgb(61, 61, 61);
  box-shadow: 5px 5px var(--black);
  color: var(--white);
  cursor: pointer;
}

.button:active {
  background-color: #51a3c9;
  transform: translate(5px, 5px);
  box-shadow: none;
}

.hidden {
  visibility: hidden;
}
